elect: none;\n  -ms-user-select: none;\n}\n\nbody {\n  font-family: 'Josefin Sans', sans-serif;\n  min-height: 100vh;\n}\n\n.main {\n  @include flex-container(column, start);\n  height: 100vh;\n  padding: 24px;\n  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),\n    url(./assets/images/background.jpg);\n}\n\n.form {\n  @include flex-container(column, start);\n  gap: 16px;\n  width: 300px;\n\n  @include box-shadow();\n  background-color: #eeeeeebb;\n  padding: 16px 24px;\n  border-radius: 12px;\n\n  legend {\n    font-size: 1.5rem;\n    font-weight: bolder;\n    color: #333;\n  }\n\n  fieldset {\n    @include flex-container(column, start, start);\n    gap: 12px;\n    width: 100%;\n    padding: 0px 12px;\n    border: none;\n  }\n\n  .pair {\n    @include flex-container(column, start, start);\n    gap: 2px;\n    width: 100%;\n\n    label {\n      color: #000;\n      font-size: 0.7rem;\n      font-weight: bolder;\n      text-transform: uppercase;\n    }\n\n    input,\n    select {\n      width: 100%;\n      padding: 4px 8px;\n      border-radius: 4px;\n      border: none;\n      outline: none;\n      @include box-shadow(#000000cc, 0, 0, 2px, 0);\n\n      &:invalid {\n        @include box-shadow(#eb000099, 0, 0, 0px, 1px);\n      }\n    }\n\n    select {\n      cursor: pointer;\n    }\n  }\n\n  button {\n    @include button($hover-bg: #274727cc);\n    background-color: #274727ee;\n    border: none;\n    outline: none;\n\n    width: 64px;\n    padding: 8px 12px;\n\n    color: #eee;\n    font-weight: bold;\n  }\n}\n"],"sourceRoot":""}]);
